<template>
    <div id="gererFourniture">
        <!-- les liens s'adapteront à l'utilisateur connecté -->
        <nav class="navbar navbar-expand-md navbar-light">
            <span class="navbar-brand mb-0 h1">Gestion des fournitures scolaires</span>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
                aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>

            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav mr-auto">
                    <li class="nav-item">
                        <a class="nav-link" href="accueil">Accueil</a>
                    </li>
                    <li class="nav-item dropdown">
                        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false">
                            Consulter
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="inv-ele">Inventaire d'un élève</a>
                            <a class="dropdown-item" href="inv-ens">Inventaire d'un enseignant</a>
                        </div>
                    </li>
                    <li class="nav-item dropdown">
                        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false">
                            Distribuer
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="distr-cla">A une classe</a>
                            <a class="dropdown-item" href="distr-ele">A un élève</a>
                            <a class="dropdown-item" href="distr-ens">A un enseignant</a>
                        </div>
                    </li>
                    <li class="nav-item active">
                        <a class="nav-link" href="gerer-fourniture">Gérer stock de fourniture</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="gerer-categorie">Gérer les catégories</a>
                    </li>
                </ul>
                <p class="utilisateur">Nom de l'utilisateur connecté</p>                
                <a href="form_conn" class="btn btn-danger" role="button">Déconnexion</a>
            </div>
        </nav>

        <div class="container-fluid col-md-8">



            <h1>Gestion des fournitures</h1>
            <div class="row justify-content-between bottom-buffer">
                <div class="col-md-6 bottom-buffer">
                    <div class="row justify-content-md-start">
                        <div class="input-group search">
                            <input type="text" class="form-control" placeholder="Rechercher une fourniture...">
                            <span class="input-group-btn">
                                <button class="btn" type="submit" id="search-btn"><i class="fa fa-search"></i></button>
                            </span>
                        </div>
                    </div>
                </div>
                <div class="col-md-5 justify-content-end"> 
                    <div class="d-flex bottom-buffer justify-content-end">
                        <a href="nouvelle-fourniture" class="btn btn-primary">Créer une nouvelle fourniture</a>
                    </div>
                    <div class="d-flex justify-content-end">
                        <a href="#" class="btn btn-primary">Récupérer une fourniture supprimée</a>
                    </div>
                </div>
            </div>
            
            <div class="table-responsive">
                <table class="table table-striped">
                    <thead class="thead-dark">
                        <tr>
                            <th scope="col">
                                Désignation
                                <!--<i class="fa fa-chevron-up"></i>-->
                                <i class="fa fa-chevron-down"></i>
                            </th>
                            <th scope="col" width="200">Prix (CHF) <i class="fa fa-chevron-down"></i></th>
                            <th scope="col" width="200">A saisir</th>
                            <th scope="col">Catégorie <i class="fa fa-chevron-down"></i></th>
                            <th scope="col" width="150">Modifier</th>
                            <th scope="col" width="150">Supprimer</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="fourniture in fournitures" v-bind:key="fourniture.index">
                            <td scope="row" id="col_fourniture">
                                {{fourniture.nom}}
                            </td>
                            <td>
                                {{fourniture.prix}}
                            </td>
                            <td>
                                <input type="checkbox" disabled class="form-check-input position-static">
                            </td>
                            <td>
                                catégorie 1, catégorie 2, ...
                            </td>
                            <td>
                                <a href="modifier-fourniture" class="btn btn-outline-primary">
                                    Modifier <i class="fa fa-pencil"></i>
                                </a>
                            </td>
                            <td>
                                <button class="btn btn-outline-danger">
                                    Supprimer <i class="fa fa-trash-o"></i>
                                </button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'gererFourniture',
    data() {
        return {
            fournitures: [],
            errors: []
        }
    },
    created() { 
        // Récupération des fournitures
        axios.get(`http://localhost:3000/fourniture`)
        .then(response => {
            // JSON responses are automatically parsed.
            this.fournitures = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })
    }

}

</script>
<!-- styling for the component -->
<style scoped>

img {
    max-width: 25px;
}

table {
    text-align: center;
}

.search {
  max-width: 400px;
  min-width: 300px;
}

</style>


